@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');
@font-face {
    font-family: GSansBlack;
    src: url('resource/font/ProductSans-Black.ttf');
}
@font-face {
    font-family: GSansBold;
    src: url('resource/font/ProductSans-Bold.ttf');
}
@font-face {
    font-family: GSansMedium;
    src: url('resource/font/ProductSans-Medium.ttf');
}
@font-face {
    font-family: GSansRegular;
    src: url('resource/font/ProductSans-Regular.ttf');
}
@font-face {
    font-family: GSansLight;
    src: url('resource/font/ProductSans-Light.ttf');
}
@font-face {
    font-family: GSansThin;
    src: url('resource/font/ProductSans-Thin.ttf');
}

html {
  background-color: #2A2A2A;
  color: #E8E8E8;
  font-family: 'GSansMedium';
}

#self-photo {
  width: calc(100% - 30px);
  aspect-ratio: 1 / 1;
  background-image: url('resource/lucas.jpg');
  background-size: cover;
  border-radius: 15px;
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  top: 15px;
  max-width: 470px
}

.self-name {
  font-size: 75px;
  font-family: 'GSansBlack';
  position: absolute;
  line-height: 60px;
  position: absolute;
  left: 20px;
  top: 370px
}
.self-name span:nth-child(1) {
  color: #2A2A2A
}
#self-name-desktop {
  display: none
}

@media screen and (min-width: 900px) {
  #self-photo {
    width: calc(100% - 30px);
    aspect-ratio: 9 / 16;
    background-image: url('resource/lucas.jpg');
    background-size: cover;
    border-radius: 15px;
    position: absolute;
    right: 15px;
    top: 15px;
    max-width: 630px;
    left: unset;
    transform: unset;
    max-height: 730px;
    background-position: center;
  }
  .self-name span:nth-child(1) {
    color: #E8E8E8
  }
  #self-name-desktop {
    font-size: 75px;
    font-family: 'GSansBlack';
    position: absolute;
    line-height: 60px;
    position: absolute;
    left: 20px;
    top: 15px;
    display: block;
    text-align: right
  }
  #self-name-mobile {
    display: none
  }
}